/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

#oncoprintDiv .oncoprintjs__track_options__toggle_btn_img {
  top: 4px; }

#oncoprintDiv .oncoprintjs__track_options__toggle_btn_img,
#oncoprintDiv .oncoprintjs__header__toggle_btn_img {
  opacity: 0;
  border-width: 0px !important;
  border-radius: 5px;
  position: relative; }
  #oncoprintDiv .oncoprintjs__track_options__toggle_btn_img:hover,
  #oncoprintDiv .oncoprintjs__header__toggle_btn_img:hover {
    background-color: #eee; }

#oncoprintDiv:hover .oncoprintjs__track_options__toggle_btn_img,
#oncoprintDiv:hover .oncoprintjs__header__toggle_btn_img {
  opacity: 1; }

#oncoprintDiv .oncoprintjs__track_options__dropdown,
#oncoprintDiv .oncoprintjs__header__dropdown {
  border: 1px solid rgba(0, 0, 0, 0.15) !important;
  margin: 0 !important;
  padding: 0 !important;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  border-radius: 5px;
  z-index: 10000; }
  #oncoprintDiv .oncoprintjs__track_options__dropdown li,
  #oncoprintDiv .oncoprintjs__header__dropdown li {
    padding: 2px 5px; }
    #oncoprintDiv .oncoprintjs__track_options__dropdown li.oncoprintjs__track_options__separator, #oncoprintDiv .oncoprintjs__track_options__dropdown li.oncoprintjs__header__separator,
    #oncoprintDiv .oncoprintjs__header__dropdown li.oncoprintjs__track_options__separator,
    #oncoprintDiv .oncoprintjs__header__dropdown li.oncoprintjs__header__separator {
      border-top: 1px solid #dddddd !important; }
    #oncoprintDiv .oncoprintjs__track_options__dropdown li,
    #oncoprintDiv .oncoprintjs__header__dropdown li {
      border-width: 0 !important; }
    #oncoprintDiv .oncoprintjs__track_options__dropdown li:hover,
    #oncoprintDiv .oncoprintjs__header__dropdown li:hover {
      background: #fff !important;
      text-decoration: underline; }
